@import '../libs/font-awesome/font-awesome.min.css';
@import '../libs/base-css/reset.css';
@import '../libs/base-css/base.css';
@font-face {
  font-family: 'ProximaNova';
  src: url("../fonts/ProximaNova/ProximaNova-Bold/ProximaNova-Bold.eot");
  src: url("../fonts/ProximaNova/ProximaNova-Bold/ProximaNova-Bold.eot?#iefix") format('embedded-opentye'), url("../fonts/ProximaNova/ProximaNova-Bold/ProximaNova-Bold.woff") format('woff'), url("../fonts/ProximaNova/ProximaNova-Bold/ProximaNova-Bold.ttf") format('truetype');
  font-style: normal;
  font-weight: bold;
}
@font-face {
  font-family: 'ProximaNova';
  src: url("../fonts/ProximaNova/ProximaNova-Regular/ProximaNova-Regular.eot");
  src: url("../fonts/ProximaNova/ProximaNova-Regular/ProximaNova-Regular.eot?#iefix") format('embedded-opentye'), url("../fonts/ProximaNova/ProximaNova-Regular/ProximaNova-Regular.woff") format('woff'), url("../fonts/ProximaNova/ProximaNova-Regular/ProximaNova-Regular.ttf") format('truetype');
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: 'FontAwesome';
  src: url("../fonts/font-awesome/fontawesome-webfont.eot");
  src: url("../fonts/font-awesome/fontawesome-webfont.eot?#iefix") format('embedded-opentye'), url("../fonts/font-awesome/fontawesome-webfont.woff") format('woff'), url("../fonts/font-awesome/fontawesome-webfont.ttf") format('truetype');
  font-style: normal;
  font-weight: normal;
}
body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  overflow-x: hidden;
}
header {
  padding: 15px;
  width: calc(100vw - 30px);
  height: calc(100vh - 30px);
  background-color: #990;
}
.box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  margin: 0 -7.5px;
}
.game-box {
  position: relative;
  margin: 0 7.5px;
  width: calc(85% - 15px);
}
.game-height {
  padding-top: 50%;
}
.game-field,
.players {
  border-radius: 5px;
  border: 3px solid #000;
}
.game-field {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
}
.players {
  margin: 0 7.5px;
  width: calc(15% - 15px);
  display: flex;
  flex-direction: column;
  height: 100%;
}
.players .player {
  flex-basis: 20%;
}
.players .player + .player {
  border-top: 3px solid #000;
}
.top-row {
  border-bottom: 3px solid #000;
  display: flex;
}
.middle-row {
  flex-grow: 2;
  display: flex;
}
.middle-row .col {
  display: flex;
}
.middle-row .col-left,
.middle-row .col-right {
  display: flex;
  flex-direction: column;
}
.bottom-row {
  border-top: 3px solid #000;
  display: flex;
}
.square {
  position: relative;
  flex-basis: 15%;
  max-width: 150px;
  flex-grow: 1;
}
.square-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.square-height {
  padding-top: 100%;
}
.cell {
  flex-basis: 6.36%;
  flex-grow: 1;
}
.col-left,
.col-right {
  flex-basis: 15%;
  max-width: 150px;
  flex-grow: 1;
}
.top-border {
  border-top: 3px solid #000;
}
.left-border {
  border-left: 3px solid #000;
}
.right-border {
  border-right: 3px solid #000;
}
.col {
  flex-basis: 70%;
  flex-grow: 1;
}
.interface {
  width: 70%;
}
.text {
  width: 30%;
}
.col-left .cell,
.col-right .cell {
  width: 100%;
  flex-basis: 18%;
}
.col-left .cell-big,
.col-right .cell-big {
  flex-basis: 28%;
}
